



































import { Component, Prop, Vue } from "vue-property-decorator";
import CoreKrImg from "./CoreKrImg.vue";

@Component({ components: { CoreKrImg } })
export default class FilePreview extends Vue {
  //v-img component
  @Prop() imgAlt!: string;
  imgSrc: string | ArrayBuffer | null = "";

  //v-select-file component
  fpModel: object | null = null;
  @Prop({ default: "primary accent-4" }) chipColor!: string;
  @Prop({ default: "primary" }) fpColor!: string;
  @Prop() fpLabel!: string;
  @Prop() fpPlaceholder!: string;
  @Prop({ default: "mdi-paperclip" }) fpPrependIcon!: string;
  @Prop({ default: true }) isRequired!: boolean;

  previewFile(file: File) {
    const reader = new FileReader();
    if (file) {
      reader.readAsDataURL(file);
    } else {
      this.imgSrc = "";
    }
    reader.onloadend = () => {
      this.imgSrc = reader.result;
    };
    this.$emit("change", file);
  }
}
