































import { Component, Prop, Vue } from "vue-property-decorator";

@Component
export default class CoreKrImg extends Vue {
  @Prop() alt!: string;
  @Prop() aspectRatio!: string;
  @Prop({ default: false }) contain!: boolean;
  @Prop({ default: false }) eager!: boolean;
  @Prop() gradient!: string;
  @Prop() height!: string;
  @Prop() lazySrc!: string;
  @Prop() maxHeight!: string;
  @Prop() maxWidth!: string;
  @Prop() minHeight!: string;
  @Prop() minWidth!: string;
  @Prop({ default: "center center" }) position!: string;
  @Prop() sizes!: string;
  @Prop() src!: string;
  @Prop() srcset!: string;
  @Prop({ default: "fade-transition" }) transition!: boolean | string;
  @Prop() width!: number | string;

  @Prop({ default: "center" }) placeholderAlignContent!: string;
  @Prop() placeholderClass!: Array<string>;
  @Prop({ default: "center" }) placeholderJustify!: string;
  @Prop() placeholderText!: string;
}
